<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  inject: ['reload'],
  data () {
    return {
      columns: [
        {
          title: 'ID',
          key: 'id',
          align: 'center',
        },
        {
          title: '名称',
          key: 'name',
          align: 'center',
        },
        {
          title: '添加时间',
          key: 'formatTime',
          align: 'center',
        },
        {
          title: '发布人',
          key: 'addadmin_name',
          align: 'center',
        },
        {
          title: '审核时间',
          key: 'reviewTime',
          align: 'center',
        },
        {
          title: '审核人',
          key: 'upadmin_name',
          align: 'center',
        },
        {
          title: '审核状态',
          key: 'state',
          align: 'center',
          render: (h, params) => {
            const status = params.row.status;
            const color = status == 1 ? 'success' : status == 2 ? 'info' : status == 3 ? 'warning' : 'error';
            const text = status == 1 ? '已发布' : status == 2 ? '隐藏' : status == 3 ? '审核中' : '已退回';

            return h('Tag', {
              props: {
                type: 'dot',
                color: color,
              }
            }, text);
          }
        },
        {
          title: '功能',
          key: 'action',
          align: 'center',
          render: (h, params) => {
            const status = params.row.status;
            if(status == 3) {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  on: {
                    click: () =>{
                      this.$router.push({
                        path: 'editReview',
                        query: {
                          'id': params.row.id,
                          'type': this.reviewType
                        }
                      })
                    }
                  },
                }, '审核')
              ]);
            } else {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  }
                }, '-')
              ]);
            }
          }
        }
      ],
      data: [],
      datePreSet: {
        shortcuts: [
          {
            text: '现在',
            value () {
              let d = new Date(),
                  str = '';
                  str += d.getFullYear() + '-';
                  str += d.getMonth() + 1 + '-';
                  str += d.getDate() + ' ';
                  str += d.getHours() + ':';
                  str += d.getMinutes() + ':';
                  str += d.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click today');
            }
          },
          {
            text: '昨天',
            value () {
              const date = new Date();
                    date.setTime(date.getTime() - 3600 * 1000 * 24);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click yesterday');
            }
          },
          {
            text: '一周前',
            value () {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click a week ago');
            }
          }
        ]
      },
      formData: {
        seltype: 'addtime',
        stime: '',
        etime: '',
        adminid: '0',
        status: '0',
        reviewid: '0',
      },
      configObj: {
        adminList: {},
        status: {},
      },
      totalPages: 0,
      isLoading: true,
      reviewType: 1,
      page: 1,
      per_page: 10
    }
  },
  created () {
    let _this = this
    this.reviewType = this.$route.path.indexOf('reviewGame') == -1 ? 2 : 1;
    if(this.reviewType == 1) {
      this.isLoading = false
      this.configObj.adminList = this.$store.state.reviewListData.adminlist;
      this.configObj.status = this.$store.state.reviewListData.config.status;
      this.totalPages = this.$store.state.reviewListData.totlerows;
      this.data = this.$store.state.reviewListData.gamelist;
      this.data.forEach(ele => {
        //格式化时间
        ele.formatTime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
        ele.reviewTime = ele.uptime ? this.$moment(ele.uptime*1000).format("YYYY-MM-DD k:mm"): '-';
      })
    } else {
      this.handleForm();
    }
    
    
    // this.reviewType = this.$route.

    document.onkeydown = function () {// 监听enter事件
      let key = window.event.keyCode;
      if (key === 13) { 
        _this.handleForm()
      }
    }
  },
  methods: {
    handleForm () {
      const token = getToken(),
          page = this.page*1,
          per_page = this.per_page*1,
          seltype = this.formData.seltype,
          stime = this.formData.stime,
          etime = this.formData.etime,
          adminid = this.formData.adminid,
          reviewid = this.formData.reviewid,
          status = this.formData.status,
          params = {token, page, per_page, seltype, stime, etime, adminid, reviewid, status};

      // console.log(params)
      this.params = params;
      this.getData();
    },
    getData () {
      this.isLoading = true;
      this.reviewType = this.$route.path.indexOf('reviewGame') == -1 ? 2 : 1;
      const path = this.$route.path.indexOf('reviewGame') == -1 ? 'reviewApp' : 'reviewGame';
      api[path](this.params).then(res => {
        // console.log(res)
        this.isLoading = false
        if(res.code == 0) {
          this.configObj.adminList = res.data.adminlist
          this.configObj.status = res.data.config.status
          this.totalPages = res.data.totlerows
          this.data = res.data.gamelist
          this.data.forEach(ele => {
            //格式化时间
            ele.formatTime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
            ele.reviewTime = ele.uptime ? this.$moment(ele.uptime*1000).format("YYYY-MM-DD k:mm"): '-';
          })
        } else {
          this.$Notice.error({
           desc: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({
          desc: err
        })
      })
    },
    changePage (page) {
      // console.log(page)
      this.page = page;
      this.handleForm();
    },
    changePageNum (num) {
      // console.log(num)
      this.page = 1;
      this.per_page = num;
      this.handleForm();
    },
    getSTime (t) {
      this.formData.stime = t;
    },
    getETime (t) {
      this.formData.etime = t;
    }
  },
  watch: {
    $route (to, from) {
      if(to.fullPath != from.fullPath) {
        this.handleForm();
      }
    }
  }
}
</script>
<template>
  <div>
    <h3 v-if="reviewType == 1"><Icon type="ios-paw" size=24 />游戏审核列表</h3>
    <h3 v-else><Icon type="ios-paw-outline" size=24 />应用审核列表</h3>
    <div class="formBox">
      <Select v-model="formData.seltype" style="width:100px">
        <Option value="addtime">添加时间</Option>
        <Option value="uptime">审核时间</Option>
      </Select>
      <DatePicker v-model="formData.stime" @on-change="getSTime" type="datetime" :options="datePreSet" placeholder="选择开始日期" style="width: 200px"></DatePicker>
      <DatePicker v-model="formData.etime" @on-change="getETime" type="datetime" :options="datePreSet" placeholder="选择结束日期" style="width: 200px"></DatePicker>
      <Select v-model="formData.adminid" style="width:100px" @on-change="handleForm">
        <Option value="0">发布人</Option>
        <Option v-for="itm,idx in configObj.adminList" :value="itm.uid" :key="idx">{{itm.username}}</Option>
      </Select>
      <Select v-model="formData.status" style="width:100px" @on-change="handleForm">
        <Option value="0">审核状态</Option>
        <Option v-for="itm,idx in configObj.status" :value="idx" :key="idx">{{itm}}</Option>
      </Select>
      <Select v-model="formData.reviewid" style="width:100px" @on-change="handleForm">
        <Option value="0">审核人</Option>
        <Option v-for="itm,idx in configObj.adminList" :value="itm.uid" :key="idx">{{itm.username}}</Option>
      </Select>
      <Button type="primary" style="width:100px" @click="handleForm">
        <Icon type="ios-search" size=16></Icon>
        搜索
      </Button>
    </div>
    <Table 
      border 
      ref="selection" 
      :columns="columns" 
      :data="data" 
      :loading=isLoading
      >
    </Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer :page-size=10 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .formBox {
    .ivu-select {
      margin: 0 20px 20px 0;
    }
    .ivu-input-wrapper {
      margin: 0 20px 20px 0;
    }
    .ivu-date-picker, .ivu-btn {
      position: relative;
      vertical-align: middle;
      line-height: normal;
      margin: 0 20px 20px 0;
    }
    .ivu-select{
      width: 160px;
    }
  }
</style>
